<template>
	<div>
		<div class="con">
			<h3>身份证明材料：</h3>
			<p>1、身份证原件正反面的扫描件</p>
			<p>2、办理身份证回执扫描件（身份证过期时需要提供）</p>
			<p>3、报考人户口本首页和个人页（学历证书姓名与身份证不一致时，提供证明有曾用名）</p>
			<p>4、报考人居住证正反面扫描件（若有）</p>
			<p>5、报考人社保卡正反面扫描件（若有）</p>
			<p>6、在照相馆拍的大一寸彩色白底证件照电子版原件</p>
		</div>
		<div class="con">
			<h3>学历证明材料：</h3>
			<p>1、学历证原件封面和内容页的扫描件</p>
			<p>2、学历鉴定报告扫描件（大专及以上学历才需要上传）</p>
			<p>3、学籍证明扫描件（若需）</p>
		</div>
		<div class="con">
			<h3>工作证明材料：</h3>
			<p>1、社保明需（若需）</p>
			<p>2、营业执照副本复印件（当工作证明上的工作单位在红盾网上查询不到时需要提供）</p>
			<p>3、工作单位的红盾网查询截图（填写的所有工作单位都需要查询截图）</p>
		</div>
		
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				
			}
		},
		methods:{
			goEach(text){
				if(text=='所需证件列表'){
					this.$router.push({name: 'Documentslist'})
				}
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	.con{padding: .3rem; font-size: .28rem; line-height: .48rem;
		h3{font-size: .3rem; color: #fa8a12;}
	}
	
</style>


